.main-container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;   

    @media screen and (min-width: 414px) {
        max-width: 414px;
    }
    @media screen and (min-width: 1024px) {
        max-width: 1024px;
    }
    @media screen and (min-width: 1280px) {
        max-width: 1280px;
    }
}